import React, { useEffect, useRef, useState } from "react";
import CustomModal from "../components/modals/customModal";

//  ImageCropModal 
interface ImageCropModalProps {
  isOpen: boolean; // 
  onClose: () => void; // 
  onConfirm: (pixelColors: string[]) => void; // 
  pixelSizeInput: number; // 
}

// ImageCropModal 
const ImageCropModal: React.FC<ImageCropModalProps> = ({
  isOpen,
  onClose,
  onConfirm,
  pixelSizeInput,
}) => {
  //  
  const [uploadedImage, setUploadedImage] = useState<File | null>(null);

  //   URL
  const [previewUrl, setPreviewUrl] = useState<string | null>(null);

  //  x, y
  const [cropBoxPosition, setCropBoxPosition] = useState({ x: 0, y: 0 });

  //  
  const [isDragging, setIsDragging] = useState(false);

  //  
  const [imageSize, setImageSize] = useState<{
    width: number;
    height: number;
  } | null>(null);

  //  
  const [minImageSize, setMinImageSize] = useState(100);

  //  0-100
  const [progress, setProgress] = useState(20);

  //  
  const cropBoxSize = (progress / 100) * minImageSize;

  //   DOM 
  const fileInputRef = useRef<HTMLInputElement>(null); // 
  const canvasRef = useRef<HTMLCanvasElement>(null); // 
  const imgRef = useRef<HTMLImageElement>(null); // 
  const containerRef = useRef<HTMLDivElement>(null); // 
  const progressRef = useRef<HTMLDivElement>(null); // 

  //  
  const handleImageUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file && file.type.startsWith("image/")) {
      const img = new Image();
      img.onload = () => {
        // 
        setImageSize({ width: img.naturalWidth, height: img.naturalHeight });
      };
      img.src = URL.createObjectURL(file); //  URL

      setUploadedImage(file); // 
      if (fileInputRef.current) fileInputRef.current.value = ""; // 
    } else {
      alert(" PNGJPG");
    }
  };

  //  
  const handleMouseDown = () => setIsDragging(true);

  //  
  const handleMouseUp = () => setIsDragging(false);

  //  
  const handleMouseMove = (e: React.MouseEvent) => {
    if (!isDragging || !containerRef.current) return;

    const rect = containerRef.current.getBoundingClientRect(); // 
    const cropBoxSize = (progress / 100) * minImageSize; // 
    const halfBox = cropBoxSize / 2; // 

    // 
    const newX = e.clientX - rect.left - halfBox;
    const newY = e.clientY - rect.top - halfBox;

    // 
    const maxX = rect.width - cropBoxSize;
    const maxY = rect.height - cropBoxSize;

    const updatedPosition = {
      x: Math.max(0, Math.min(newX, maxX)),
      y: Math.max(0, Math.min(newY, maxY)),
    };

    setCropBoxPosition(updatedPosition); // 
    generateLivePreview(updatedPosition); // 
  };

  //  
  const handleTouchStart = (e: React.TouchEvent) => {
    e.preventDefault();
    setIsDragging(true);
  };

  //  
  const handleTouchEnd = () => {
    setIsDragging(false);
  };

  //  
  const handleTouchMove = (e: React.TouchEvent) => {
    if (!isDragging || !containerRef.current) return;

    const rect = containerRef.current.getBoundingClientRect();
    const cropBoxSize = (progress / 100) * minImageSize;
    const halfBox = cropBoxSize / 2;

    const touch = e.touches[0]; // 
    const newX = touch.clientX - rect.left - halfBox;
    const newY = touch.clientY - rect.top - halfBox;

    const maxX = rect.width - cropBoxSize;
    const maxY = rect.height - cropBoxSize;

    const updatedPosition = {
      x: Math.max(0, Math.min(newX, maxX)),
      y: Math.max(0, Math.min(newY, maxY)),
    };

    setCropBoxPosition(updatedPosition);
    generateLivePreview(updatedPosition);
  };

  //  
  const generateLivePreview = (position = cropBoxPosition) => {
    const img = imgRef.current;
    if (!img || !canvasRef.current) return;

    const naturalWidth = img.naturalWidth; // 
    const naturalHeight = img.naturalHeight; // 
    const renderedWidth = img.clientWidth; // 
    const renderedHeight = img.clientHeight; // 

    // 
    const scaleX = naturalWidth / renderedWidth;
    const scaleY = naturalHeight / renderedHeight;

    // 
    const realX = position.x * scaleX;
    const realY = position.y * scaleY;
    const realSize = cropBoxSize * scaleX;

    // 
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");

    // 
    canvas.width = pixelSizeInput;
    canvas.height = pixelSizeInput;

    // 
    ctx?.drawImage(
      img,
      realX,
      realY,
      realSize,
      realSize,
      0,
      0,
      pixelSizeInput,
      pixelSizeInput
    );

    setPreviewUrl(canvas.toDataURL()); //  URL
  };

  //  
  const handleCropConfirm = () => {
    const img = imgRef.current;
    const canvas = canvasRef.current;
    if (!img || !canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const naturalWidth = img.naturalWidth;
    const naturalHeight = img.naturalHeight;
    const renderedWidth = img.clientWidth;
    const renderedHeight = img.clientHeight;

    const scaleX = naturalWidth / renderedWidth;
    const scaleY = naturalHeight / renderedHeight;

    const realX = cropBoxPosition.x * scaleX;
    const realY = cropBoxPosition.y * scaleY;
    const realSize = cropBoxSize * scaleX;

    // 
    canvas.width = pixelSizeInput;
    canvas.height = pixelSizeInput;

    // 
    ctx.clearRect(0, 0, pixelSizeInput, pixelSizeInput);
    ctx.drawImage(
      img,
      realX,
      realY,
      realSize,
      realSize,
      0,
      0,
      pixelSizeInput,
      pixelSizeInput
    );

    // 
    const imageData = ctx.getImageData(
      0,
      0,
      pixelSizeInput,
      pixelSizeInput
    ).data;
    const pixelColors: string[] = [];

    // 
    for (let i = 0; i < imageData.length; i += 4) {
      const r = imageData[i].toString(16).padStart(2, "0");
      const g = imageData[i + 1].toString(16).padStart(2, "0");
      const b = imageData[i + 2].toString(16).padStart(2, "0");
      pixelColors.push(`#${r}${g}${b}`);
    }

    onConfirm(pixelColors); // 
  };

  //  
  const handleProgressClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (!progressRef.current) return;

    const rect = progressRef.current.getBoundingClientRect();
    const x = e.clientX - rect.left; // 
    const width = rect.width;

    let percent = (x / width) * 100; // 
    percent = Math.max(0, Math.min(100, percent)); // 

    setProgress(Math.round(percent)); // 
  };

  //  
  useEffect(() => {
    if (imageSize) {
      setMinImageSize(Math.min(imageSize.width, imageSize.height));
    } else {
      setMinImageSize(100); // 
    }
  }, [imageSize]);

  //  
  useEffect(() => {
    if (uploadedImage) {
      setIsDragging(true);
      setTimeout(() => {
        generateLivePreview(cropBoxPosition); // 
      }, 0);
      setIsDragging(false);
    }
  }, [progress]);

  return (
    //  
    <CustomModal
      isOpen={isOpen}
      onConfirm={handleCropConfirm} // 
      onClose={() => {
        setUploadedImage(null); // 
        setPreviewUrl(null); // 
        onClose(); // 
      }}
      isShowClose={true} // 
      hasWidth // 
    >
      {/*   */}
      <div className="p-3 bg-info bg-opacity-10 border border-info rounded">
        <div className="text-start">
          <label className="form-label"></label>
        </div>

        <input
          id="file"
          type="file"
          ref={fileInputRef}
          accept="image/*"
          onChange={handleImageUpload}
          className="form-control"
          style={{ display: "block" }}
        />
      </div>

      {/*   */}
      <div
        ref={containerRef}
        style={{
          position: "relative",
          width: imageSize ? `${Math.min(imageSize.width, 400)}px` : "auto", // 
          height: imageSize ? `${Math.min(imageSize.height, 400)}px` : "auto", // 
          overflow: "hidden",
        }}
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      >
        {uploadedImage && (
          <img
            ref={imgRef}
            src={URL.createObjectURL(uploadedImage)}
            alt="preview"
            style={{
              display: "block",
              maxWidth: "100%",
              maxHeight: "100%",
              objectFit: "contain", // 
              margin: "0 auto", // 
            }}
          />
        )}

        {/*   */}
        {uploadedImage && (
          <div
            style={{
              position: "absolute",
              top: cropBoxPosition.y,
              left: cropBoxPosition.x,
              width: cropBoxSize,
              height: cropBoxSize,
              border: "2px dashed red",
              boxSizing: "border-box",
              pointerEvents: "none", // 
              zIndex: 10,
            }}
          />
        )}

        {/*   */}
        {uploadedImage && (
          <div
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              width: "100%",
              height: "100%",
              backgroundColor: "transparent",
              zIndex: 5,
            }}
          />
        )}

        {/*   */}
        <canvas ref={canvasRef} style={{ display: "none" }} />
      </div>

      {/*   */}
      {uploadedImage && (
        <div
          ref={progressRef}
          className="progress mt-3"
          style={{ width: "100%", height: "20px", cursor: "pointer" }}
          onClick={handleProgressClick}
        >
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: `${progress}%` }}
            aria-valuenow={progress}
            aria-valuemin={0}
            aria-valuemax={100}
          >
            {progress}%
          </div>
        </div>
      )}

      {/*   */}
      {previewUrl && (
        <div style={{ marginTop: "20px" }}>
          <h4></h4>
          <img
            src={previewUrl}
            alt="crop-preview"
            style={{
              width: `${pixelSizeInput * 4}px`, // 
              height: `${pixelSizeInput * 4}px`,
              imageRendering: "pixelated", // 
              border: "1px solid #ccc",
            }}
          />
        </div>
      )}
    </CustomModal>
  );
};

export default ImageCropModal;
