import React from "react";
import { Link, useLocation } from "react-router-dom"; //  useLocation 
import { ROUTES } from "../../constants/routes"; // 
import routes from "../../config/routes";

const Navbar: React.FC = () => {
  const location = useLocation(); // 

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light shadow-sm fixed-top">
      <div className="container">
        <Link className="navbar-brand text-dark" to={ROUTES.HOME}>
          yee 
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav ms-auto">
            {/*  routes  */}
            {routes
              .filter((route) => route.showInNavbar) // 
              .map((route) => (
                <li
                  className={`nav-item ${location.pathname === route.path ? "active" : ""}`} //  active 
                  key={route.path}
                >
                  <Link className="nav-link text-dark" to={route.path}>
                    {route.name}
                  </Link>
                </li>
              ))}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
