import React, { useState } from "react";
import {
  Container,
  Carousel,
  Accordion,
  Modal,
  Button,
  Table,
  Badge,
  ProgressBar,
} from "react-bootstrap";
import projectProgressData from "../config/projectsConfig";
import { Project } from "../types/project";

const HomePage: React.FC = () => {
  const [showModal, setShowModal] = useState(false);
  const [selectedProject, setSelectedProject] = useState<Project | null>(null); //table

  //  Badge
  const getStatusBadge = (status?: string) => {
    switch (status) {
      case "":
        return <Badge bg="success"></Badge>;
      case "":
        return <Badge bg="warning"></Badge>;
      case "":
        return <Badge bg="danger"></Badge>;
      case "":
        return <Badge bg="primary"></Badge>;
      default:
        return <Badge bg="secondary"></Badge>;
    }
  };

  const handleShow = (project: Project) => {
    setSelectedProject(project);
    setShowModal(true);
  };

  const handleClose = () => {
    setShowModal(false);
    setSelectedProject(null);
  };

  return (
    <div className="home-page">
      {/* Carousel Section */}
      <section className="py-5">
        <Container>
          <Carousel>
            <Carousel.Item>
              <div
                className="d-flex justify-content-center align-items-center"
                style={{ height: "25vh" }}
              >
                <img
                  src="img01.jpg"
                  alt="1"
                  style={{ height: "100%", width: "auto" }}
                />
              </div>
              <div
                className="carousel-caption-container"
                style={{
                  textAlign: "center",
                  padding: "10px",
                  backgroundColor: "rgba(0, 0, 0, 0.5)", // 
                  color: "white",
                }}
              >
                <h3></h3>
                <p></p>
              </div>
            </Carousel.Item>
            <Carousel.Item>
              <div
                className="d-flex justify-content-center align-items-center"
                style={{ height: "25vh" }}
              >
                <img
                  src="img02.jpg"
                  alt="2"
                  style={{ height: "100%", width: "auto" }}
                />
              </div>
              <div
                className="carousel-caption-container"
                style={{
                  textAlign: "center",
                  padding: "10px",
                  backgroundColor: "rgba(0, 0, 0, 0.5)", // 
                  color: "white",
                }}
              >
                <h3></h3>
                <p></p>
              </div>
            </Carousel.Item>
            <Carousel.Item>
              <div
                className="d-flex justify-content-center align-items-center"
                style={{ height: "25vh" }}
              >
                <img
                  src="img03.jpg"
                  alt="3"
                  style={{ height: "100%", width: "auto" }}
                />
              </div>
              <div
                className="carousel-caption-container"
                style={{
                  textAlign: "center",
                  padding: "10px",
                  backgroundColor: "rgba(0, 0, 0, 0.5)", // 
                  color: "white",
                }}
              >
                <h3></h3>
                <p></p>
              </div>
            </Carousel.Item>
          </Carousel>
        </Container>
      </section>

      {/* Accordion - FAQ */}
      <Container className="py-5">
        <Accordion>
          {/* Accordion Item 1 */}
          <Accordion.Item eventKey="0">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
              
            </Accordion.Body>
          </Accordion.Item>

          {/* Accordion Item 2:  */}
          <Accordion.Item eventKey="1">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
              
            </Accordion.Body>
          </Accordion.Item>

          {/* Accordion Item 3:  */}
          <Accordion.Item eventKey="2">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
              SEO
              
            </Accordion.Body>
          </Accordion.Item>

          {/* Accordion Item 4:  */}
          <Accordion.Item eventKey="3">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
              
              LOGO(CI)
            </Accordion.Body>
          </Accordion.Item>

          {/* Accordion Item 5:  */}
          <Accordion.Item eventKey="4">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
              
            </Accordion.Body>
          </Accordion.Item>

          {/* Accordion Item 6:  (UX/UI) */}
          <Accordion.Item eventKey="5">
            <Accordion.Header> (UX/UI)</Accordion.Header>
            <Accordion.Body>
               UX/UI
              
            </Accordion.Body>
          </Accordion.Item>

          {/* Accordion Item 7:  */}
          <Accordion.Item eventKey="6">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
              
            </Accordion.Body>
          </Accordion.Item>

          {/* Accordion Item 8:  */}
          <Accordion.Item eventKey="7">
            <Accordion.Header></Accordion.Header>
            <Accordion.Body>
              
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
      </Container>

      {/* Table Section */}
      <Container className="py-5">
        <h2 className="mb-4"></h2>
        <Table striped bordered hover responsive>
          <thead className="bg-light">
            <tr>
              <th>#</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {projectProgressData.map((project) => (
              <tr key={project.id}>
                <td>{project.id}</td>
                <td>{project.name}</td>
                <td>{getStatusBadge(project.status)}</td>
                <td>{project.dueDate}</td>
                <td>
                  <Button
                    variant="info"
                    size="sm"
                    onClick={() => handleShow(project)}
                  >
                    
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
        {/* Modal  */}
        <Modal show={showModal} onHide={handleClose} size="lg">
          <Modal.Header closeButton>
            <Modal.Title></Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {selectedProject && (
              <div>
                <h4>{selectedProject.name}</h4>
                <p>
                  <strong></strong>
                  {getStatusBadge(selectedProject.status)}
                </p>
                <p>
                  <strong></strong>
                  {selectedProject.dueDate}
                </p>
                <p>
                  <strong></strong>
                  {selectedProject.description}
                </p>
                <p>
                  <strong></strong>
                  {selectedProject.projectManager}
                </p>
                <p>
                  <strong></strong>
                  {selectedProject.budget} 
                </p>
                <p>
                  <strong></strong>
                  {selectedProject.progress}%
                </p>
              </div>
            )}
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              
            </Button>
          </Modal.Footer>
        </Modal>
      </Container>

      {/* Progress Section */}
      <Container>
        <h2></h2>
        <div className="position-relative mb-4">
          <ProgressBar
            now={70}
            variant="success"
            style={{ height: "30px" }} // 
          />
          <span
            className="position-absolute w-100 text-center"
            style={{
              top: "50%",
              left: 0,
              right: 0,
              transform: "translateY(-50%)",
            }}
          >
            70%
          </span>
        </div>
        <p>
          
          70%
        </p>
      </Container>
    </div>
  );
};

export default HomePage;
