import React, { useState, useEffect } from "react";
import { Container, Spinner, OverlayTrigger, Tooltip } from "react-bootstrap";

const AboutPage: React.FC = () => {
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // 
    setTimeout(() => {
      setIsLoading(false); //  spinner
    }, 500);
  }, []);

  if (isLoading) {
    return (
      <Container className="text-center py-3">
        <Spinner animation="border" variant="primary" />
      </Container>
    );
  }

  return (
    <div className="about-page">
      {/* Card: About Header with Tooltip */}
      <section className="bg-light py-5">
        <div className="container">
          <div className="card shadow-lg bg-info-subtle text-info-emphasis">
            <div className="card-body text-center">
              <OverlayTrigger
                placement="top"
                overlay={
                  <Tooltip id="tooltip-about">
                    
                  </Tooltip>
                }
              >
                <h2 className="card-title mb-4"></h2>
              </OverlayTrigger>
              <p className="lead card-text">
                
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Card: Company Story Section with Popover */}
      <section className="bg-light py-5">
        <div className="container">
          <div className="card shadow-lg">
            <div className="card-body text-center">
              <h2 className="card-title mb-4"></h2>
              <p className="card-text lead">
                2010
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Card: Our Team Section */}
      <section className="bg-light py-5">
        <div className="container">
          <div className="card shadow-lg">
            <div className="card-body text-center">
              <h2 className="card-title mb-4"></h2>
              <div className="row justify-content-center">
                <div className="col-md-4 mb-4">
                  <img
                    src="img06.jpg"
                    alt="1"
                    className="rounded-circle shadow-lg"
                    style={{ width: "150px", height: "150px" }}
                  />
                  <h3 className="mt-3"></h3>
                  <p className="text-muted">
                    
                  </p>
                </div>
                <div className="col-md-4 mb-4">
                  <img
                    src="img02.jpg"
                    alt="2"
                    className="rounded-circle shadow-lg"
                    style={{ width: "150px", height: "150px" }}
                  />
                  <h3 className="mt-3">yee</h3>
                  <p className="text-muted"></p>
                </div>
                <div className="col-md-4 mb-4">
                  <img
                    src="img03.jpg"
                    alt="3"
                    className="rounded-circle shadow-lg"
                    style={{ width: "150px", height: "150px" }}
                  />
                  <h3 className="mt-3"></h3>
                  <p className="text-muted">
                    
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Card: Our Mission Section */}
      <section className="bg-light py-5">
        <div className="container">
          <div className="card shadow-lg">
            <div className="card-body text-center">
              <h2 className="card-title mb-4"></h2>
              <p className="card-text lead">
                
              </p>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default AboutPage;
